.add-txn-popup-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
  position: relative;
  /* width: 100%; */
  padding: 30px;
}

.transaction-btn {
  border: 0;
  outline: none;
  border-radius: 12px;
  background: #2d60ff;
  color: #fff;
  padding: 10px;
}

.add-txn-input {
  padding: 8px;
  outline: none;
  border: 1px solid #dfeaf2;
}
.add-txn-input::placeholder {
  color: #718ebf;
}

.add-txn-heading-text {
  font-size: 24px;
  margin: 0;
  margin-bottom: 12px;
}
.add-txn-para-text {
  margin: 0;
  margin-bottom: 12px;
  color: #505887;
}
.input-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 12px;
}

.submit-btn {
  background-color: #2d60ff;
  color: #fff;
  outline: none;
  border: 0px;
  padding: 8px;
  border-radius: 6px;
  width: 100%;
}
.span-el {
  color: rgb(234, 30, 30);
}
